import React from "react";
import Header from "components/header/Header.js";
/* redux initialize */
import { compose, applyMiddleware, createStore } from "redux";
import { persistStore, autoRehydrate } from "redux-persist";
import { Provider } from "react-redux";
import reducer from "data/reducers";
import { createLogger } from "redux-logger";
/* redux connect */
import { connect } from "react-redux";
import * as actions from "data/actions";
/* redux store */
const logger = createLogger({
	collapsed: true
});
const store = createStore(reducer, applyMiddleware(logger));
// const store = createStore(reducer, compose(applyMiddleware(logger), autoRehydrate()));
/*
	Original layout template
*/
class Layout extends React.Component {
	componentWillReceiveProps(props) {
		if (props.area_key !== undefined) {
			console.log("componentDidMount", props);
			props.dispatch(actions.my_area(props.area_key));
		}
	}
	render() {
		console.log("Layout", this.props.area_key);
		return (
			<div className="Layout" style={Object.assign({ minHeight: "100vh" }, this.props.style || {})}>
				<Header />
				{this.props.children || null}
			</div>
		);
	}
}
/*
	Connect redux data
*/
const mapStateToProps = (data, ownProps) => {
	const data_area = data.areas[ownProps.area_key || ""];
	return {
		items: Object.assign({}, ownProps.items),
		items_status: data.my.items_status || "new",
		filters: data.my.filters || []
	};
};
const ConnectedLayout = connect(mapStateToProps)(Layout);
/*
	Layout as the page component will receive it
*/
class LayoutExport extends React.Component {
	render() {
		// if (this.props.purge) {
		// 	persistStore(store)
		// 		.purge()
		// 		.then(function(what) {
		// 			console.info("PURGE", what);
		// 		})
		// 		.catch(function(what) {
		// 			console.error("PURGE", what);
		// 		});
		// } else {
		// 	persistStore(store);
		// }
		const { children, ...props } = this.props;
		return (
			<Provider store={store}>
				<ConnectedLayout {...props}>{children}</ConnectedLayout>
			</Provider>
		);
	}
}

export default LayoutExport;



